.component-login-modal .ant-modal-title{
    text-align: center;
    font-size: 1.6vw;
    color: #323232;
}

.component-login-modal .ant-modal-header{
    border-bottom: none;
    padding-top: 1.6vw;
    padding-bottom: 1.6vw;
}

.component-login-modal .ant-modal-body{
    padding:0 3.6vw;
}

.component-login-form .ant-form-item{
    margin-bottom: 0;
}

.component-login-form input{
    height:60px;
    box-sizing: border-box;
}

#component-login-form_phoneNo,
#component-login-form_certNo,
.component-login-form input,
.component-login-form input:focus,
.component-login-form input:hover {
    border-radius: 0.3vw;
    border: solid 0.1vw #dbdbdb;
    outline: none;
    box-shadow: none;
}

#component-login-form_phoneNo{
    border-bottom: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;

}

#component-login-form_certNo,
.component-login-form .ant-input-group-addon{
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

#component-login-form_certNo{
    border-bottom-right-radius: 0;
}

.component-login-form .ant-input-group-addon{
    padding:0;
}

.component-login-form .ant-input-group-addon .btn-addon{
    cursor: pointer;
    color:#ff3a2e;               
}
.component-login-form .ant-input-group-addon .info{
    padding: 0 11px;
    height:60px;
    min-width: 95px;
    box-sizing: border-box;
}

.component-login-form .login-form-button{
    margin:1.6vw 0;
    height:50px;
    font-size: 24px;
}